import { MetaHTMLAttributes } from "vue";

let appComponent: HTMLElement | undefined = undefined  // elemento HTML che contiene l'app, inizializzato all'init

const LS = {
  THEME: 'themeSelected'
}

export const helper = {

  device: undefined as {type: string, size: number, class: string} | undefined,

  init(mainComponent?: HTMLElement){
    /* resizing */
    window.addEventListener('resize', onWindowResize)
    appComponent = mainComponent;
    onWindowResize();
    /* theme */
    this.theme.change(localStorage.getItem(LS.THEME) || 'dark');
    /* splash screen */
    // if(sessionStorage.getItem('opened')){
    //   // la pagina era preventivamente aperta
    //   // this.splashScreen.remove(true);
    // } else {
      this.splashScreen.removeConditionFromQueue('app');
      const performanceEntry = performance.getEntriesByName('splashScreenLoaded');      
      setTimeout(() => {
        this.splashScreen.removeConditionFromQueue('timeout');
        sessionStorage.setItem('opened', 'true');
      }, Math.max(0, (performanceEntry?.length && performanceEntry[0].startTime) ? performanceEntry[0].startTime + 2500 - performance.now() : 2500));
    // }
  },


  theme: {

    name: '',

    change(newThemeName: string){
      /* cambio aspetto nel componente principale them-...*/
      if(appComponent){
        const classList = appComponent?.classList.toString().split(' ');
        for(let i = 0; i < classList.length; i++) {
          if(classList[i].includes('theme-') && classList[i] !== `theme-${newThemeName}`) {
            appComponent?.classList.remove(classList[i])
          }
        }
        appComponent.classList.add(`theme-${newThemeName}`);
      }
      /* cambio meta tag */
      const tags = document.getElementsByTagName('head')[0].children;
      let exits = false;
      for(let i = 0; i < tags.length; i++){
        if(tags[i].tagName === 'META' && (tags[i] as MetaHTMLAttributes).name === 'color-scheme'){
          (tags[i] as MetaHTMLAttributes).content = newThemeName;
          exits = true;
          break;
        }
      }
      if(!exits){
        const meta = document.createElement('meta');
        meta.name = 'color-scheme';
        meta.content = newThemeName;
        document.getElementsByTagName('head')[0].appendChild(meta);
      }
      /* salvo il tema */
      this.name = newThemeName;
      localStorage.setItem(LS.THEME, newThemeName);
    },
  },


  splashScreen: {
    queueCondition: {
      timeout: true,
      app: true,
    },

    removeConditionFromQueue: function (name: string) {
      (this.queueCondition as any)[name] = false;
      let queueEnd = true
      for(const key in this.queueCondition) {
        queueEnd = queueEnd && !(this.queueCondition as any)[key];
      }
      if(queueEnd){
        this.remove();
      }
    },

    remove: function (immediately: boolean = false) {
      const splashScreen = document.getElementById('splashScreen');
      if (!splashScreen) return;

      if (immediately) {
        document.body.removeChild(splashScreen);
        return;
      }
      splashScreen.style.opacity = '0';
      setTimeout(() => {
        document.body.removeChild(splashScreen);
      }, 450); // Tempo di transizione in millisecondi
    }
  },
  
}

const responsiveSizes = [
  { type: 'mobile', size: 768, class: 'device-mobile' },
  { type: 'tablet', size: 1024, class: 'device-tablet' },
  { type: 'desktop', size: 1440, class: 'device-desktop' },
]

function onWindowResize () {
  const windowWidth = window.innerWidth;
  let currentResponiveDevice = responsiveSizes[responsiveSizes.length - 1]; 
  for (let i = 0; i < responsiveSizes.length; i++) {
    if (windowWidth <= responsiveSizes[i].size) {
      currentResponiveDevice = responsiveSizes[i];
      break;
    }
  }
  helper.device = currentResponiveDevice;
  if(appComponent){
    const classList = appComponent?.classList.toString().split(' ');
    responsiveSizes.forEach((device) => {
      if (device.type !== currentResponiveDevice.type && classList.includes(device.class)) {
        appComponent?.classList.remove(device.class);
      } else if (device.type === currentResponiveDevice.type && !classList.includes(device.class)) {
        appComponent?.classList.add(device.class);
      }
    });
  }
}